<template>
  <div class='collapse_item' :style='styleObj'>
    <div class='collapse_content'>
      <div class='collapse_all_height' ref='collapse_all_height'>
        <span class='collapse_title'>
          <slot v-if='$slots.title' name='title'></slot>
          <span v-else-if='title'>{{ title }}</span>
        </span>
        <span class='collapse_right_title' @click="switchCollapse">
          <slot name='right' v-if='$slots.right'></slot>
          <span v-else-if='rightTitle'>{{ rightTitle }}</span>
        </span>
        <span>
          <slot name='default'></slot>
        </span>
      </div>
    </div>
  </div>
</template>



<script>
export default {
  data() {
    return {
      active: false,
      currentHeight: "",
      styleObj: {},
    };
  },
  props: {
    title: {
      type: String,
      default: "",
    },
    rightTitle: {
      type: String,
      default: "",
    },
    name: {
      type: [String, Number],
      default() {
        return this._uid;
      },
    }
  },

  created() {
    this.initHeight();
  },

  inject: ["collapse", "height"],

  watch: {
    "collapse.activeNames": {
      handler(newVal) {
        setTimeout(() => {
          if (newVal.includes(this.name)) {
            this.active = true;
          } else {
            this.active = false;
          }
          this.isExpand();
        }, 50);
      },
    },
  },

  methods: {
    // 初始化高度
    initHeight() {
      var height = (this.active ? this.currentHeight : this.height) + "px";
      this.styleObj = {
        lineHeight: this.height + "px",
        height: height,
      };
    },
    // 是否展开
    isExpand() {
      if (
        this.$refs.collapse_all_height &&
        this.$refs.collapse_all_height.offsetHeight > this.height + 5
      ) {
        this.currentHeight = this.$refs.collapse_all_height.offsetHeight;
      } else {
        this.currentHeight = this.height;
      }
      this.initHeight();
    },
    // 切换折叠面板
    switchCollapse() {
      this.active = !this.active;
      this.collapse && this.collapse.switchCollapse(this);
    },
  },
};
</script>

<style scoped lang='less'>
.collapse_item {
  width: 100%;
  border-bottom: 1px solid #ebeef5;
  border-top: 1px solid #ebeef5;
  overflow: hidden;
  transition: all 0.3s linear;
  position: relative;
  .collapse_content {
    padding: 0px;
    padding-right: 25px;
    background-color: white;
    .collapse_title {
      font-size: 13px;
    }
    .collapse_all_height {
      display: inline-block;
      .collapse_right_title {
        position: absolute;
        right: 0px;
        font-size: 13px;
        margin-right: 10px;
        cursor: pointer;
      }
    }
  }
}
</style>