<template>
  <div class="carModelDetail">
    <vxe-modal v-model="value" class='carModelDetail'  title='车型详情' @hide="$emit('input', false)" id="inReceiptSelectOrder" width="850" :esc-closable='true' destroy-on-close height="650" min-width="700" min-height="100" resize remember storage transfer>
      <div class="carModelContent">
        <el-form :inline="true" :model="formData" class="demo-form-inline">
          <el-form-item label="厂家：" size="mini">
            <span style="font-size:12px">
              {{formData.show_name}}
            </span>
          </el-form-item>
          <el-form-item label="品牌：" size="mini">
            <span>{{formData.brand_name }}</span>
          </el-form-item>
          <el-form-item label="车系：" size="mini">
            {{formData.car_series}}
          </el-form-item>
          <el-form-item label="车型：" size="mini">
            {{formData.car_type}}
          </el-form-item>
          <el-form-item label="年款：" size="mini">
            {{formData.years }}
          </el-form-item>
          <el-form-item label="上市时间：" size="mini">
            {{formData.c_variant_sop}}
          </el-form-item>
          <el-form-item label="停产时间：" size="mini">
            {{formData.c_variant_eop }}
          </el-form-item>
          <el-form-item label="国产合资进口类型：" size="mini">
            {{formData.joint_type }}
          </el-form-item>
          <el-form-item label="排放标准：" size="mini">
            {{formData.emission_standard }}
          </el-form-item>
          <el-form-item label="最大功率（kw）：" size="mini">
            {{formData.max_power }}
          </el-form-item>
          <el-form-item label="驱动形式：" size="mini">
            {{formData.drive_mode }}
          </el-form-item>
          <el-form-item label="变速器类型：" size="mini">
            {{formData.transmission_type }}
          </el-form-item>
          <el-form-item label="变速器描述：" size="mini">
            {{formData.transmission_desc }}
          </el-form-item>
          <el-form-item label="档位数：" size="mini">
            {{formData.gear_number }}
          </el-form-item>
          <el-form-item label="发动机型号：" size="mini">
            {{formData.engine_type }}
          </el-form-item>
          <el-form-item label="排量(升)：" size="mini">
            {{formData.displacement }}
          </el-form-item>
          <el-form-item label="气缸数(个)：" size="mini">
            {{formData.cylinder_num }}
          </el-form-item>
          <el-form-item label="燃油类型：" size="mini">
            {{formData.fuel_type }}
          </el-form-item>
          <el-form-item label="车门数：" size="mini">
            {{formData.door_num }}
          </el-form-item>
          <el-form-item label="座位数（个）：" size="mini">
            {{formData.seats_num }}
          </el-form-item>
        </el-form>
      </div>
      <div class="dialog-footer">
        <el-button @click="$emit('input', false)">关 闭</el-button>
      </div>
    </vxe-modal>
  </div>
</template>

<script>
import get_car_librarys_csv from "common/components/plugin/service/config/carModel.js";
export default {
  data() {
    return {
      formData: {}
    };
  },
  watch: {
    value(newVal) {
      if (newVal === true) {
        this.getData();
      }
    }
  },
  props: {
    value: null,
    car_lib_id: {
      type: String,
      default: ''
    }
  },
  created() {
    get_car_librarys_csv(res => {
      this.allData = res.data
    });
  },
  methods: {
    getData() {
      for(var i in this.allData) {
        for(var j of this.allData[i]) {
          if (j.car_lib_id == this.car_lib_id) {
            this.formData = j
          }
        }
      }
    }
  }
};
</script>

<style  lang='less'>
.carModelDetail {
  .carModelContent {
    .el-form-item__label {
      width: 150px;
    }
  }
}
</style>