<template>
  <div>
    <div class="Accordion">
      <div class="SelectedList fcl">
        <div style="margin-right:50px">
          <span class="lable" v-if="tagObj">已选条件：</span>
          <el-tag v-if="tagObj.oneType" size="medium" key="oneType" closable @close="removeTag('oneType')">
            {{ type == 1 ? "一级分类" : "二级分类" }} :
            {{ tagObj.oneType.gc_name }}
          </el-tag>
          <el-tag v-if="tagObj.twoType" size="medium" key="twoType" closable @close="removeTag('twoType')">
            {{ type == 1 ? "二级分类" : "三级分类" }} :
            {{ tagObj.twoType.gc_name }}
          </el-tag>
          <el-tag v-if="tagObj.brand" size="medium" key="brand" closable @close="removeTag('brand')">
            品牌：{{ tagObj.brand.brand_name }}
          </el-tag>
          <el-tag v-if="tagObj.carModel" size="medium" key="carModel" closable @close="removeTag('carModel')">
            车型：{{ tagObj.carModel.sales_name }}
          </el-tag>
        </div>
      </div>
      <erpCollapse v-model="activeNames" accordion :height="30">
        <erpCollapseItem :title="type == 1 ? '一级分类:' : '二级分类：' + ''" name="2" rightTitle="更多">
          <el-tag v-for="(item, index) in oneTypes" :key="index" :type="tagObj.oneType.gc_id == item.gc_id ? 'danger' : ''" @click="typeChange(item, 1)">{{ item.gc_name }}</el-tag>
        </erpCollapseItem>
        <erpCollapseItem :title="type == 1 ? '二级分类：' : '三级分类：' + ''" name="3" rightTitle="更多" :disabled="!tagObj.oneType">
          <el-tag v-for="(item, index) in twoTypes" :key="index" :type="tagObj.twoType.gc_id == item.gc_id ? 'danger' : ''" @click="typeChange(item, 2)">{{ item.gc_name }}
          </el-tag>
        </erpCollapseItem>
      </erpCollapse>
      <div v-if="brandList.length" class="searchItem">
        <erpCollapse v-model="brandBox" accordion :height="30">
          <erpCollapseItem title="品牌列表：" name="2" rightTitle="更多">
            <el-tag v-for="(item, index) in brandList" :key="index" @click="brandChange(item, index)" :type="tagObj.brand.brand_id == item.brand_id ? 'danger' : ''">{{ item.brand_name }}</el-tag>
          </erpCollapseItem>
        </erpCollapse>
      </div>

      <div v-if="carModelBox.length" class="searchItem">
        <span class="brandtitle">车型列表：</span>
        <el-tag v-for="(item, index) in carModelBox" :key="index" @click="carModelChange(item, index)" :type="tagObj.carModel.car_lib_id == item.car_lib_id ? 'danger' : ''">
          {{ item.sales_name }}
          <el-button type="text" style="line-height: 5px;" @click.stop="
              () => {
                car_lib_id = item.car_lib_id;
                isShowCarModelDetail = true;
              }
            ">详情</el-button>
        </el-tag>
      </div>

      <carModelDetail :car_lib_id="car_lib_id" v-model="isShowCarModelDetail" ref="carModelDetail" />
    </div>
  </div>
</template>

<script>
import erpCollapse from "./erpCollapse";
import erpCollapseItem from "./erpCollapseItem";
import carModelDetail from "./carModelDetail";
export default {
  // service_station/open/Goods/getAllClassAndBrand
  props: {
    searchParams: {
      type: Object,
      default: () => {},
    },
    type: {
      type: Number,
      default: 1, // 1是采购中心  2是服务站销售开单
    },
  },
  data() {
    return {
      activeNames: [],
      brandList: [], //品牌
      brandBox: [],
      allBrandList: [], // 全部品牌
      isShowCarModelDetail: false,
      tagObj: {
        oneType: "",
        twoType: "",
        brand: "",
        carModel: "",
      },
      carModelBox: [],
      car_lib_id: "",
      oneTypes: [],
      twoTypes: []
    };
  },
  methods: {
    removeTag(key) {
      this.tagObj[key] = "";
      if (key === "oneType") {
        this.clear();
      } else if (key === "twoType") {
        this.tagObj.brand = "";
      } else if (key === "carModel") {
        this.carModelBox = [];
      }
      this.search();
    },
    // 添加车型
    _addCarModel(val) {
      this.carModelBox = val;
      if (val.length) {
        this.carModelChange(val[0]);
      }
    },
    // 车型改变
    carModelChange(val) {
      this.tagObj.carModel = val;
      this.search();
    },
    // 品牌改变
    brandChange(item) {
      this.tagObj.brand = item;
      this.search();
    },
    search() {
      this.searchParams.gc_id = this.tagObj.twoType
        ? this.tagObj.twoType.gc_id
        : this.tagObj.oneType
        ? this.tagObj.oneType.gc_id
        : "";
      this.searchParams.brand_id = this.tagObj.brand.brand_id;
      this.searchParams.car_lib_id = this.tagObj.carModel.car_lib_id;
      this.$emit("search", this.tagObj);
    },
    // 分类改变
    typeChange(item, index) {
      if (index === 1) {
        this.twoTypes = item.child;
        this.tagObj.oneType = item;
        this.tagObj.twoType = "";
      } else {
        this.tagObj.twoType = item;
      }
      this.tagObj.brand = "";
      this.search();
    },
    initClassList(data) {
      var result = [];
      if (this.type === 1) {
        data.class_list.forEach((val) => {
          result = result.concat(val.child);
        });
        this.classList = result;
      } else {
        for (var i in data.goods_type_list) {
          var child = data.goods_type_list[i].child
            ? data.goods_type_list[i].child
            : [];
          var obj = {
            gc_id: i,
            gc_name: data.goods_type_list[i].type_info.goods_type_name,
            child: child.map((val) => {
              val.gc_id = val.goods_type_id;
              val.gc_name = val.goods_type_name;
              return val;
            }),
          };
          result.push(obj);
        }
        this.classList = result;
      }
      this.renderClass(this.renderObj);
      this.renderBrank(this.renderObj);
    },
    // 渲染分类
    renderClass({ class_list }) {
      this.twoTypes = [];
      this.oneTypes = [];
      this.classList.forEach((val) => {
        var step = false;
        val.child.forEach((twoVal) => {
          for (var i = 0; i < class_list.length; i++) {
            if (class_list[i] - 0 === twoVal.gc_id - 0) {
              step = true;
              this.twoTypes.push(twoVal);
            }
          }
        });
        step && this.oneTypes.push(val);
      });
    },
    init() {
      var ajaxAction;
      if (this.type === 1) {
        ajaxAction = this.$axMethod["getClassAndBrandList"]();
      } else if (this.type == 2) {
        ajaxAction = this.$api["serviceStationGetAllClassAndBrand"]();
      }
      ajaxAction.then(({ data }) => {
        if (data.code == 200) {
          this.$emit("InitializeThe", data.data);
          this.allBrandList = data.data.brand_list.concat([]);
          this.initClassList(data.data);
          this.renderBrank(this.renderObj);
          this.renderClass(this.renderObj);
        }
      });
    },
    clear() {
      this.tagObj = {
        oneType: "",
        twoType: "",
        brand: "",
        carModel: "",
      };
      this.carModelBox = [];
      this.search();
      this.activeNames = [];
    },
    // 渲染品牌
    renderBrank({ brand_list }) {
      this.brandList = this.allBrandList.filter((val) => {
        for (var i = 0; i < brand_list.length; i++) {
          if (brand_list[i] - 0 === val.brand_id - 0) {
            return true;
          }
        }
        return false;
      });
    },

    // 回显列表的分类品牌
    echoSearch(data) {
      if (data.code === 200) {
        this.renderObj = data.data;
        this.renderObj = {
          brand_list: data.data.brand_list ? data.data.brand_list : [],
          class_list: data.data.class_list ? data.data.class_list : [],
        };
        this.renderBrank(this.renderObj);
        this.renderClass(this.renderObj);
      }
    },
  },
  mounted() {
    this.classList = [];
    this.renderObj = { brand_list: [], class_list: [] };
    this.init();
  },
  components: {
    erpCollapse,
    erpCollapseItem,
    carModelDetail,
  },
};
</script>

<style lang="less" scoped>
.Accordion {
  /deep/.el-collapse-item__header {
    height: 48px;
    line-height: 48px;
    font-size: 13px;
    color: #303133;
    border-bottom: 1px solid #ebeef5;
  }
  .el-collapse {
    padding: 0;
  }
  .el-tag--medium {
    cursor: pointer;
    margin: 5px;
  }
  .SelectedList {
    min-width: 1000px;
    .lable {
      font-size: 13px;
      color: #303133;
    }
  }
  .searchItem {
    background-color: white;
    border-bottom: solid 1px #ebeef5;
    padding-top: 2px;
  }
  /deep/.el-collapse-item__content {
    padding: 0px;
  }
}
.brandtitle {
  font-size: 13px;
  color: #303133;
}
/deep/.el-tag--small {
  cursor: pointer;
}
</style>
