<template>
  <span>
    <el-input v-model="value" size="small" @input="$emit('input', value)" maxlength="20" placeholder="输入VIN码或点击相机传图" clearable @change="$emit('search')" @keyup.enter.native="$emit('search')">
      <i slot="suffix" @click="
          isShowIdentifyVin = !isShowIdentifyVin;
          isIdentifyingLoading = 0;
        " class="el-input__icon el-icon-camera"></i>
    </el-input>
    <el-popover placement="top" width="160" v-model="isShowIdentifyVin" class="el-popover-useTutorial">
      <div class="useTutorialBox">
        <div v-if="isIdentifyingLoading === 0">
          <div class="useTutorialImg">
            <el-input type="textarea" contenteditable="true" @paste.native="copyImag" placeholder="拖拽或复制图片到这里"></el-input>
            <span class="content">拖拽或复制图片到这里</span>
          </div>
          <div>
            <input type="file" @change="uploadImg" />
            <el-button type="primary">选择文件</el-button>
          </div>
        </div>
        <div v-else-if="isIdentifyingLoading === 1">
          <div class="loadingBox">
            正在加载图片. . .
          </div>
        </div>
        <div v-else>
          <img :src="imgurl" alt="" />
        </div>
        <span class="close" @click="
            isShowIdentifyVin = false;
            isIdentifyingLoading = 0;
          ">
          <i class="el-icon-close"></i>
        </span>
      </div>
    </el-popover>
  </span>
</template>

<script>
import { copyImg, getBase64Image } from "common/utils/editImg";
export default {
  data() {
    return {
      isIdentifyingLoading: 0,
      isShowIdentifyVin: false,
    };
  },
  props: {
    value: {
      type: String || Number,
      default: "",
    },
  },
  mounted() {
    this.dropImg();
  },
  methods: {
    _close() {
      this.isShowIdentifyVin = false;
    },
    dropImg() {
      var that = this;
      var oBox = this.$myDocument.getElementById("dropImg");
      oBox.addEventListener("dragover", function (e) {
        e.stopPropagation();
        e.preventDefault();
      });
      oBox.ondrop = function (e) {
        e.stopPropagation();
        e.preventDefault();
        var oFile = e.dataTransfer.files[0];
        that.isIdentifyingLoading = 1;
        getBase64Image(oFile).then((data) => {
          that.imgurl = data;
          that.identifyImageVin();
        });
      };
    },
    uploadImg(e) {
      var oFile = e.target.files[0];
      var name = oFile.name
        .substring(oFile.name.lastIndexOf(".") + 1)
        .toLocaleLowerCase();
      if (name == "jpg" || name == "jpeg" || name == "png" || name == "bmp") {
        this.isIdentifyingLoading = 1;
        getBase64Image(oFile).then((data) => {
          this.imgurl = data;
          this.identifyImageVin();
        });
      } else {
        e.target.valle = "";
        this.$message.warning("请上传图片！");
      }
    },
    copyImag(e) {
      this.isIdentifyingLoading = 1;
      copyImg(e)
        .then((data) => {
          this.imgurl = data;
          this.identifyImageVin();
        })
        .catch(() => {
          // this.$message.warning("请把图片打开再复制！");
          // this.isIdentifyingLoading = 0;
        });
    },
    // 图片识别
    identifyImageVin() {
      this.$axMethod
        .identifyImageVin({ image: this.imgurl })
        .then(({ data }) => {
          if (data.code == 200) {
            this.$emit("input", data.data.vin_code);
            this.isShowIdentifyVin = true;
            this.isIdentifyingLoading = 2;
          } else {
            this.isIdentifyingLoading = 0;
          }
        })
        .catch(() => {
          this.isIdentifyingLoading = 0;
        });
    },
  },
};
</script>

