<template>
  <div class="params" id="dropImg" v-loading="RampLoad">
    <div id="leixin">
      <div class="search_container">
        <!-- 采购中心订单 -->
        <el-button
          style="width: auto;"
          type="primary"
          size="mini"
          class="useTutorial"
          @click="isShowUseTutorial = true"
          >用户教程</el-button
        >
        <el-form
          :inline="true"
          :model="params"
          class="demo-form-inline"
          ref="ruleForm"
          :rules="rules"
        >
          <el-form-item label="关键字:" size="mini" style="width: 412px;">
            <el-input
              v-model="params.keyword"
              maxlength="100"
              style="width:300px"
              placeholder="请输入库位码/名称/品牌/OE码/商品类目"
              clearable
              @keyup.enter.native="onSearch()"
            ></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch()" size="mini" type="primary"
              >查询</el-button
            >
          </el-form-item>
          <el-form-item label="VIN码:" size="mini" style="width: 400px;">
            <vinCode v-model="params.vin" ref="vinCode" @search="vinSearch()" />
            <el-button
              type="primary"
              style="width: auto;"
              size="mini"
              @click="vinSearch()"
              >VIN查询</el-button
            >
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button
              style="width: auto;"
              type="primary"
              size="mini"
              :class="!showModelIsLoad ? 'is-disabled' : ''"
              @click="openSelectModel"
              >选择车型</el-button
            >
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div>
        <Classification
          :searchParams="params"
          @search="search()"
          @InitializeThe="RampLoad = false"
          ref="Classification"
        ></Classification>
      </div>
      <div>
        <div class="add_delet">
          <el-checkbox v-model="checked" @change="changeCheck"
            >隐藏库存为0商品</el-checkbox
          >
          <!-- <el-button icon="el-icon-shopping-bag-1" type="primary" @click="orderAdd" class="addBtn" size="mini">批量购买</el-button> -->
          <el-button
            icon="el-icon-plus"
            style="margin-left:20px"
            type="primary"
            @click="BatchAddToCart"
            size="mini"
            >批量加入购物车</el-button
          >

          <el-button
            style="float:right"
            icon="el-icon-shopping-cart-full"
            :loading="buttonLoadding"
            type="primary"
            @click="goShoppingCart"
            class="addBtn "
            size="mini"
            >{{ buttonLoadding ? "正在加载..." : "购物车" }}
            <span
              :class="{ doudong: AnimationState }"
              class="clycle "
              v-if="!buttonLoadding"
              >{{ car_count }}</span
            >
          </el-button>
          <el-button
            style="float:right"
            icon="el-icon-shopping-bag-1"
            type="primary"
            @click="orderAdd"
            class="addBtn"
            size="mini"
            >一键补货</el-button
          >
        </div>
        <xTable
          ref="xTable"
          :indexSign="indexSign"
          :rowStyle="rowClassName"
          :tableColumn="tableColumn"
          :selectConfig="{ checkMethod }"
          @getData="dealData"
          :ajax="{ url: 'MoneyMangetgetGoodsList', params: params }"
        />
      </div>
      <div class="specialBorderDiv">
        <p>注意</p>
        <div>
          <span>预售商品：</span>预售商品预计在下单后5-10个工作日内发货<br />
          <div>"我的库存"为您当前所属服务站"{{ Company_name }}"组织库存量</div>
        </div>
      </div>
    </div>

    <confirmOrder
      v-if="isShowConfirmOrder"
      :data="confirmOrderData"
      @close="isShowConfirmOrder = false"
    />

    <selectModel
      v-model="isShowModel"
      ref="selectModel"
      @sumbitData="carModelChange"
      @load="showModelIsLoad = true"
    />

    <useTutorial v-if="isShowUseTutorial == true" v-model="isShowUseTutorial" />
    <!-- <Modal @Cancel="Cancel" :Status="Status"></Modal> -->
    <modelDetail
      ref="library"
      v-model="Status"
      :library_id="idsArry"
      :shop_goods_id="shop_goods_id"
      :type="2"
    ></modelDetail>
    <el-dialog
      title="提示"
      :visible.sync="centerDialogVisible"
      width="400px"
      center
    >
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="商品名称:">
          <span>{{ form.goods_name }}</span>
        </el-form-item>
        <el-form-item label="预计购买量:">
          <el-input v-model="form.goods_num"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="ReplenSubmit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { tableHeader, paramsModel } from "./config/orderIndexHead";
import Modal from "./config/modal.vue";
import Classification from "common/components/plugin/service/goodsTypeCar/Classification.vue";
import selectModel from "common/components/plugin/service/selectModel.vue";
import vinCode from "common/components/plugin/service/vinCode.vue";
import useTutorial from "./config/useTutorial.vue";
import modelDetail from "common/components/plugin/service/modelDetail.vue";
export default {
  name: "jybdpurchaseorder",
  mixins: [tableHeader],
  components: {
    Modal,
    Classification,
    selectModel,
    useTutorial,
    vinCode,
    modelDetail
  },
  data() {
    return {
      testObj: Object.freeze({ id: 1 }),
      // confirmOrderData: {},
      isShowConfirmOrder: false,
      isShowSelectCar: false,
      isShowModel: false,
      showModelIsLoad: false,
      Status: false, //适配车型弹框
      idsArry: "",
      shop_goods_id: null,
      isShowIdentifyVin: false,
      isIdentifyingLoading: 0,
      isShowUseTutorial: false,
      imgurl: "",
      form: {},
      AnimationState: false,
      Company_name: localStorage.getItem("company_name"),
      RampLoad: true,
      centerDialogVisible: false,
      checked: false,
      loading: false,
      indexSign: [], //错误行，
      buttonLoadding: true,
      car_count: 0, //购物车数量
      isShowCarTypes: false,
      multipleSelection: [], //已选项
      params: this.$publicFution.deepCopy(paramsModel),
      newresult: [],
      rules: {
        oe_code: [
          { min: 4, message: "OE码至少需要填写三位以上", trigger: "blur" }
        ]
      }
    };
  },

  methods: {
    // 打开选择车型弹框
    openSelectModel() {
      if (!this.showModelIsLoad) {
        this.$message.warning("正在初始化车型库数据，请稍等！");
        return;
      }
      this.isShowModel = true;
    },
    Cancel(value) {
      this.Status = value;
    },
    changeCheck(val) {
      Object.assign(this.params, {
        has_stocks: val ? 1 : 0
      });
      this.$nextTick(() => {
        this.$refs.xTable._getData();
      });
    },
    rowClassName({ row }) {
      if (row.is_cart_goods) {
        return {
          backgroundColor: "#fdecea",
          color: "#5F5F5F"
        };
      }
    },
    // 车型改变
    carModelChange(val) {
      this.$refs.Classification._addCarModel(val);
      this.isShowModel = false;
    },
    checkMethod(val) {
      if (val.row.inventory_goods_id) {
        return true;
      } else {
        return false;
      }
    },
    // 获取匹配车型
    getFairModel() {
      if (this.params.vin) {
        this.$axMethod
          .getCarLibraryByVin({ vin: this.params.vin })
          .then(({ data }) => {
            if (data.code == 200) {
              this.$refs.Classification.carModelBox = data.data;
              if (data.data.length) {
                this.$refs.Classification.carModelChange(data.data[0]);
              }
            }
          });
      }
    },
    onReset() {
      this.params = this.$publicFution.deepCopy(paramsModel);
      this.$refs.Classification.clear();
      this.$nextTick(() => {
        this.$refs.xTable._getData();
      });
    },
    goShoppingCart() {
      this.$router.push({ path: "/purchase/shoppingCart" });
    },
    change(val) {
      Object.assign(this.params, {
        gc_id: val.gc_id || 0
      });
      this.$nextTick(() => {
        this.$refs.xTable._getData();
      });
    },
    brandChange(val) {
      if (typeof val != "undefined") {
        Object.assign(this.params, {
          brand_id: val.brand_id
        });
      } else {
        Object.assign(this.params, {
          brand_id: ""
        });
      }
      this.$nextTick(() => {
        this.$refs.xTable._getData();
      });
    },
    orderAdd() {
      this.$api.getStocksWaringGoodsList().then(({ data }) => {
        if (data.code == 200) {
          Object.assign(this.params, data.data);
          this.$nextTick(() => {
            this.$refs.xTable._getData();
          });
        }
      });
    },
    ShoppingCartToAdd(Parameter, row = {}) {
      this.AnimationState = false;
      this.$axMethod
        .addShoppingCartGoods({
          goods_list: JSON.stringify(Parameter)
        })
        .then(({ data }) => {
          if (data.code == 200) {
            // this.$message({
            //   message: data.msg,
            //   type: "success",
            // });
            this.buttonLoadding = false;
            this.$set(this, "car_count", data.data.car_count);
            this.$nextTick(() => {
              this.$refs.xTable.clearCheckboxRow &&
                this.$refs.xTable.clearCheckboxRow();
            });
            if (Object.keys(row).length) {
              row.is_cart_goods = true;
              row.cart_goods_goods_num += row.goods_num;
            } else {
              this.$nextTick(() => {
                this.$refs.xTable._getData();
              });
            }
            this.AnimationState = true;
          } else {
            this.buttonLoadding = false;
          }
        });
    },
    BatchAddToCart() {
      if (this.multipleSelection.length) {
        let goods_list = this.multipleSelection.map(res => {
          return {
            shop_goods_id: res.shop_goods_id,
            goods_num: res.goods_num
          };
        });
        this.ShoppingCartToAdd(goods_list);
      } else {
        this.$message({
          message: "请选择批量加入购物车商品",
          type: "warning"
        });
      }
    },
    search() {
      // this.params.vin = "";
      this.$refs.xTable._getData();
      this.$refs.vinCode._close();
    },
    vinSearch() {
      var vin = this.params.vin;
      this.$refs.Classification.clear();
      this.params = Object.assign({}, { vin: vin });
      this.getFairModel();
      this.$refs.vinCode._close();
    },
    onSearch() {
      // this.params.vin = "";
      this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$nextTick(() => {
            this.$refs.xTable._getData();
          });
        }
      });
      this.$refs.vinCode._close();
    },
    dealData({ data }) {
      this.$refs.Classification.echoSearch(data);
    }
  },
  activated() {
    let inventory_goods_id = this.$route.params.inventory_goods_id || "";
    if (
      inventory_goods_id &&
      inventory_goods_id != this.params.inventory_goods_id
    ) {
      this.params.inventory_goods_id = inventory_goods_id;
      this.$nextTick(() => {
        this.$refs.xTable._getData();
      });
    }
    this.buttonLoadding = true;
    this.$axMethod
      .getShoppingCartGoodsCount({
        hideLoding: true
      })
      .then(({ data }) => {
        if (data.code == 200) {
          this.$set(this, "car_count", data.data.car_count);
          this.buttonLoadding = false;
        } else {
          this.buttonLoadding = false;
        }
      });
  }
};
</script>

