<template>
  <div>
    <vxe-modal v-model="value" class="selectModel" title='选择车型' @hide="$emit('input', false)" id="inReceiptSelectOrder" width="1250" :esc-closable='true' destroy-on-close height="600" min-width="600" min-height="100" resize remember storage transfer>
      <div class="el_tree_box">
        <div class='brand'>
          <span class='brandTitle' @click="searchParams.brand = '';active = 0">品牌</span>
          <el-link :underline="false" v-for='(val, key) in letterBox' :class="currentLetter === val ? 'activeClass hoverClass' : 'hoverClass'" :key='key' @click="searchBrand(val)">{{ val }}</el-link>
          <el-input v-show='active === 0' v-model.trim="keywords" placeholder="品牌检索" clearable class="search-input" @change="keywordSearch"></el-input>
        </div>

        <!-- <div v-show='active > 0'>
          <span>已选车型： {{ searchParams.brand }} {{ searchParams.model }} {{ searchParams.displacement }}</span>
        </div> -->
        <!-- 品牌 -->

        <div class='carContainer' @scroll="scrollHandler" ref='carContainer'>
          <div ref="brandBox0" class='brandBox'>

            <template v-if="!currentLetter">
              <div v-show='!searchParams.brand'>
                <div v-if="isLoading" class="loadingText">Loading...</div>
                <template v-else>
                  <div v-for='(value,letter) in calcAllBrandBox' :key="letter">
                    <p class="brandHorizontalTitle">{{letter}}</p>
                    <hr>
                    <div class='brandFlex'>
                      <span class="brandItem" :class="searchParams.brand === key ? 'activeClass hoverClass' : 'hoverClass'" v-for='(val,key) in calcAllBrandBox[letter]' :key="key" @click="selecBrand(key, letter,val)">
                        <img :src="val[0] && val[0].brand_logo" alt="">
                        <span class='brandName' v-html="highlightKeywords(key)" :title='key'></span>
                      </span>
                    </div>
                  </div>
                </template>
                <div v-if='Object.keys(calcAllBrandBox).length === 0' style='text-align:center'>
                  暂无数据
                </div>
              </div>
              <div v-show='searchParams.brand'>
                <div class='brandFlex'>
                  <span class="brandItem activeClass">
                    <img :src="searchParams.brand_logo" alt="">
                    <span class='brandName'>{{ searchParams.brand }}</span>
                  </span>
                </div>
              </div>
            </template>
            <template v-else>
              <div class='brandFlex'>
                <span class="brandItem" :class="searchParams.brand === key ? 'activeClass hoverClass' : 'hoverClass'" v-for='(val,key) in BrandBox' :key="key" @click="selecBrand(key, letter,val)">
                  <img :src="val[0] && val[0].brand_logo" alt="">
                  <span class='brandName' v-html="highlightKeywords(key)"></span>
                </span>
              </div>
              <div v-if='Object.keys(BrandBox).length === 0' style='text-align:center'>
                暂无数据
              </div>
            </template>

          </div>

          <!-- 车系 -->
          <div ref="brandBox1" class='carSeries' v-show='active >= 1'>
            <div>
              <span class="carSeriesTitle">全部车系</span>
              <span class='carSeriesDesc'> 共 {{ carSerieslength }}个车系 , {{ carTypeLength }}个车型</span>
            </div>
            <hr>
            <div v-for='(key) in carSeriesTitleOrder' :key="key" class='carSeriesBox'>
              <el-row :gutter="20">
                <el-col :span='3'>
                  <el-button type='text' :title="key" size='mini'>{{ key }}</el-button>
                </el-col>
                <el-col :span='21'>
                  <div v-for='(twoKey) in carSeriesOrder' :key="twoKey">
                    <template v-if='carSeriesBox[key][twoKey]'>
                      <el-row class="carSeriesBoxRow">
                        <el-col :span='2' class='carSeriesBoxTitle'>
                          {{ twoKey }}：
                        </el-col>
                        <el-col :span='22'>
                          <span :title='ThreeKey' v-for='(threeVal,ThreeKey) in carSeriesBox[key][twoKey]' :key="ThreeKey" @click="selecModel(threeVal,twoKey + ThreeKey)" class='carSeriesBoxItem'>
                            <el-popover placement="bottom" width="230" :key='ThreeKey' popper-class='selectModelPopover'>
                              <div class='displacementYears'>
                                <div>
                                  <h1 class='displacementYearsTitle'>选择排量</h1>
                                  <div v-if='active >= 2'>
                                    <div v-for='(fourVal,fourKey) in displacementBox' :key="fourKey" @click="selecDisplacement(fourKey,twoKey + ThreeKey + fourKey)">
                                      <span :class="searchParams.displacement === twoKey + ThreeKey + fourKey ? 'activeClassTwo displacementYearsName' : 'displacementYearsName'">{{fourKey}}</span>
                                    </div>
                                  </div>
                                </div>
                                <div>
                                  <h1 class='displacementYearsTitle'>选择年款</h1>
                                  <div v-if='active >= 3'>
                                    <div v-for='(fourVal,fourKey) in yearsBox' :key="fourKey" @click="submit(fourVal)">
                                      <span class='displacementYearsName'>{{fourKey}}</span>
                                    </div>
                                  </div>
                                </div>
                              </div>
                              <el-button slot="reference" :class="searchParams.model === twoKey + ThreeKey ? 'activeClassTwo' : ''" type='text' :title='ThreeKey' size='mini'>{{ ThreeKey }}</el-button>
                            </el-popover>
                          </span>

                        </el-col>
                      </el-row>
                    </template>
                  </div>
                </el-col>
              </el-row>
            </div>
          </div>
        </div>
      </div>
      <div v-if="!isLoading && showToTop" class="scrollToTop" @click="scrollToTop">
        <p class="icon"></p>返回顶部
      </div>
    </vxe-modal>
  </div>
</template>

<script>
import get_car_librarys_csv from "common/components/plugin/service/config/carModel.js";
import { Throttle } from "common/utils/public.js";
import { getAllBrandBox, searchQuery, getCarSeriesBox } from "common/components/plugin/service/config/selectModel.js";
export default {
  data() {
    var letterBox = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
    return {
      letterBox: letterBox.split(""),
      carTypeLength: 0,
      carSerieslength: 0,
      currentLetter: "",
      active: 0,
      carSeriesOrder: "轿车,SUV,MPV,跑车,新能源,客车,其他".split(","),
      carSeriesTitleOrder: [],
      searchParams: {
        brand: "",
        model: "",
        displacement: "",
      },
      isLoading: true,
      keywords: "", // 品牌搜索关键字
      isKeywordsSwitch: '',
      allBrandBox: {}, // 所有品牌信息
      filterBrandBox: {}, // 所有品牌信息筛选结果
      showToTop: false, // 是否显示返回顶部
      BrandBox: {}, // 品牌对象
      carSeriesBox: {}, // 车系对象
      displacementBox: {}, // 排量
      yearsBox: {}, //年款
    };
  },
  computed: {
    calcAllBrandBox() {
      return this.isKeywordsSwitch ? this.filterBrandBox : this.allBrandBox;
    },
  },
  props: {
    value: null,
    data: {
      type: Object,
      default: () => {},
    },
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      this.allData = [];
      this.isLoading = true;
      const that = this;
      setTimeout(() => {
        get_car_librarys_csv((res) => {
          that.allBrandBox = Object.freeze(getAllBrandBox(res.data));
          that.isLoading = false;
          that.$emit("load");
        });
      }, 0);
    },
    // 高亮关键词
    highlightKeywords(value) {
      if (!this.keywords || !value.includes(this.keywords)) return value;
      const keywordsREGEXP = new RegExp(this.keywords, "ig");
      const val = value.replace(
        keywordsREGEXP,
        `<span style="color:red;">${this.keywords}</span>`
      );
      return val;
    },
    // 品牌检索(关键词搜索)
    keywordSearch() {
      this.isLoading = true;
      var result = searchQuery({
        list: this.allBrandBox,
        letter: this.currentLetter,
        keywords: this.keywords,
      });
      if (this.currentLetter) {
        this.BrandBox = Object.freeze(result);
      } else {
        this.filterBrandBox = Object.freeze(result);
      }
      this.isLoading = false;
      this.isKeywordsSwitch = this.keywords
    },
    // 品牌查询
    searchBrand(val) {
      this.keywords = ""; // 切换品牌字母索引时，清空搜索关键字
      if (!val || this.currentLetter === val) {
        // 展示所有品牌
        this.isLoading = false;
        this.currentLetter = "";
        return;
      }
      this.currentLetter = val;
      // 展示指定字母索引下的品牌
      this.BrandBox = this.allBrandBox[val] || {};
      this.isLoading = true;
      this.searchParams.brand = "";
      this.active = 0;
    },
    // 选择品牌
    selecBrand(name, letter, val) {
      this.searchParams.brand = name;
      const tempArr = this.currentLetter
        ? this.BrandBox[name]
        : this.allBrandBox[letter][name];
      var obj = getCarSeriesBox(tempArr)
      console.log(obj)
      this.carSeriesBox = obj.obj;
      this.carTypeLength = obj.total
      this.carSerieslength = obj.carSeriesArr.length
      var importBox = [];
      this.carSeriesTitleOrder = Object.keys(this.carSeriesBox)
        .filter((val) => {
          if (val.indexOf("进口") === -1) {
            return true;
          }
          importBox.push(val);
        })
        .concat(importBox);
      this.active = 1;
      this.searchParams.brand_logo = val[0].brand_logo;
    },
    selecModel(arr, name) {
      this.searchParams.model = name;
      var obj = {};
      arr.forEach((val) => {
        if (!obj[val.displacement]) {
          obj[val.displacement] = [];
        }
        obj[val.displacement].push(val);
      });
      this.displacementBox = obj;
      this.active = 2;
    },
    selecDisplacement(name, key) {
      var obj = {};
      this.displacementBox[name].forEach((val) => {
        if (!obj[val.years]) {
          obj[val.years] = [];
        }
        obj[val.years].push(val);
      });
      this.yearsBox = obj;
      this.active = 3;
      this.searchParams.displacement = key;
    },

    submit(val) {
      this.active = 1;
      this.searchParams.displacement = "";
      this.$emit("sumbitData", val);
      [...document.getElementsByClassName("selectModelPopover")].forEach(
        (val) => {
          val.style.display = "none";
        }
      );
    },

    // 置顶
    scrollToTop() {
      // 当前步骤下的滚动元素置顶
      this.$refs.carContainer.scrollTo(0, 0);
      this.$nextTick(() => {
        this.showToTop = false;
      });
    },
    // 滚动事件处理函数
    scrollHandler: Throttle(function (e) {
      this.showToTop = e.target.scrollTop >= 50;
    }, 200),
  },
};
</script>

