import { Debounce } from "@/utils/public";
export const paramsModel = {
  vin: "",
  goods_id: "",
  goods_name: "",
  brand_id: "",
  gc_id: ""
};

export const tableHeader = {
  data() {
    return {
      disabledStack: false,
      tableColumn: [
        {
          type: "checkbox",
          width: 44,
          align: "center"
        },
        {
          title: "操作",
          width: 100,
          fixed: "right",
          slots: {
            default: scope => {
              if (scope.row.goods_storage > 0) {
                return [
                  <el-button
                    disabled={scope.row.inventory_goods_id ? false : true}
                    type="text"
                    size="small"
                    onClick={() => {
                      this.AddCart(scope.row);
                    }}
                  >
                    加购
                  </el-button>
                ];
              } else {
                return [
                  <el-button
                    disabled={scope.row.inventory_goods_id ? false : true}
                    type="text"
                    size="small"
                    onClick={() => {
                      this.BackOrder(scope.row);
                    }}
                  >
                    申请补货
                  </el-button>
                ];
              }
            }
          }
        },
        {
          title: "库位码",
          field: "goods_sn",
          width: 150,
          slots: {
            default: scope => {
              if (scope.row.is_pre_sales) {
                return [
                  <span style={{ color: "red", paddingRight: "10px" }}>
                    预售
                  </span>,
                  <span>{scope.row.goods_sn}</span>
                ];
              } else {
                return [
                  <span style={{ color: "red", paddingRight: "10px" }}>
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                  </span>,
                  <span>{scope.row.goods_sn}</span>
                ];
              }
            }
          }
        },
        {
          title: "商品名称",
          field: "goods_name",
          minWidth: 200,
          slots: {
            default: (scope, h) => {
              return [<a
                style={{ cursor: "pointer" }}
                onClick={() => {
                  this.seeF(scope.row);
                }}
                type="text"
                size="small"
              >
                {scope.row.goods_name}{" "}
              </a>
              ]
            }
          }
        },
        {
          title: "品牌",
          field: "brand_name",
          width: 100
        },
        {
          title: "分类",
          field: "goods_type_name",
          width: 100
        },
        {
          title: "商品型号",
          field: "goods_model",
          width: 150,
          slots: {
            default: scope => {
              if (scope.row.goods_vat - 0) {
                return [
                  <i class="el-icon-star-on" style={{ color: "red" }}></i>,
                  <span>{scope.row.goods_model}</span>
                ];
              } else {
                return [
                  <i class="Placeholder"></i>,
                  <span>{scope.row.goods_model}</span>
                ];
              }
            }
          }
        },
        {
          title: "OE码",
          field: "oe_code",
          minWidth: 100
        },
        {
          title: "适配车型",
          headerAlign: "left",
          align: "left",
          slots: {
            default: scope => {
              return [
                <el-link
                  type="primary"
                  target={!scope.row.library_id ? "_blank" : "primary"}
                  disabled={!scope.row.library_id}
                  onClick={() => {
                    this.idsArry = scope.row.library_id;
                    this.shop_goods_id = scope.row.shop_goods_id;
                    this.$refs.library
                      .GetValue(scope.row.library_id)
                      .then(({ data }) => {
                        if (Object.keys(data.data.car_library).length) {
                          this.Status = true;
                        }
                      });
                  }}
                >
                  {scope.row.shop_car_library_attribute}
                </el-link>
              ];
            }
          }
        },
        {
          title: "销售价",
          field: "goods_price",
          headerAlign: "right",
          align: "right",
          width: 100,
          formatter: "priceFormat"
        },
        {
          title: "总库存",
          field: "goods_storage",
          width: 80
        },
        {
          title: "起订量",
          field: "order_batch_quantity",
          headerAlign: "right",
          align: "right",
          width: 80,
          slots: {
            default: scope => {
              return [
                <span>
                  {scope.row.order_batch_quantity}
                  {scope.row.unit_name
                    ? "/" + scope.row.unit_name
                    : scope.row.unit_name}
                </span>
              ];
            }
          }
        },
        {
          title: "我的库存",
          field: "erp_stocks",
          headerAlign: "right",
          width: 80,
          align: "right"
        },
        {
          title: "已加购数量",
          field: "cart_goods_goods_num",
          width: 90
        },
        {
          title: "订货数量",
          field: "goods_num",
          headerAlign: "center",
          width: 100,
          slots: {
            // 使用 JSX 渲染
            default: ({ row, rowIndex }) => {
              return [
                <div class="AddShoppingCart fcsb">
                  <el-link
                    class="widthSpan"
                    underline={false}
                    disabled={
                      row.goods_num <= row.order_batch_quantity ? true : false
                    }
                    onClick={() => this.ReduceShoppingCart(row, rowIndex)}
                  >
                    <i class="el-icon-minus"></i>
                  </el-link>
                  <el-input
                    type="number"
                    class="inputCenter"
                    v-model={row.goods_num}
                    onInput={value => {
                      this.inptChange(row, value);
                    }}
                  ></el-input>
                  <el-link
                    class="widthSpan"
                    underline={false}
                    disabled={
                      row.goods_num + row.order_batch_quantity >
                        row.goods_storage
                        ? true
                        : false
                    }
                    onClick={() => this.AddShoppingCart(row, rowIndex)}
                  >
                    <i class="el-icon-plus"></i>
                  </el-link>
                </div>
              ];
            }
          }
        }
      ]
    };
  },
  methods: {
    ReduceShoppingCart(row) {
      row.goods_num -= row.order_batch_quantity;
      this.Sum(row);
    },
    AddShoppingCart(row) {
      row.goods_num += row.order_batch_quantity;
      this.Sum(row);
    },
    AddCart(row) {
      let Cart = {
        shop_goods_id: row.shop_goods_id,
        goods_num: row.goods_num
      };
      this.ShoppingCartToAdd([Cart], row);
    },
    BackOrder(row) {
      //申请补货
      this.form = Object.assign({}, row);
      this.centerDialogVisible = true;
    },
    ReplenSubmit() {
      this.$axMethod
        .addReplenishment({
          goods_id: this.form.goods_id,
          goods_num: this.form.goods_num,
          inventory_goods_id: this.form.inventory_goods_id
        })
        .then(({ data }) => {
          if (data.code == 200) {
            this.$message({
              message: data.msg,
              type: "success"
            });
          }
        });
      this.centerDialogVisible = false;
    },
    // BuyNow(row) {
    //   this.confirmOrderData = {
    //     type: 0,
    //     data: [row],
    //   };
    //   this.isShowConfirmOrder = true;

    // },
    seeF(row) {
      //跳转
      this.$router.push({
        path: "/purchase/jybdorder/particulars",
        query: {
          shop_goods_id: row.shop_goods_id
        }
      });
    },
    Sum(row) {
      var obj = {
        num: "goods_num", // 商品数量的key
        price: "goods_price", // 商品价格的key
        goodsPrice: "goods_total_price" // 商品总金额的key  数量 * 单价
      };
      this.$publicFution.computeTotal(row, obj, "goods_num", this);
    },
    inptChange: Debounce(function (row, value) {
      if (Number(value) < row.order_batch_quantity) {
        this.$set(row, "goods_num", row.order_batch_quantity);
      } else {
        //订货数量大于起订量
        if (Number(value) > row.goods_storage) {
          //输入数量大于库存数量
          this.$set(
            row,
            "goods_num",
            parseInt(Number(row.goods_storage) / row.order_batch_quantity) *
            row.order_batch_quantity
          );
        } else {
          this.$set(
            row,
            "goods_num",
            parseInt(Number(value) / row.order_batch_quantity) *
            row.order_batch_quantity
          );
        }
      }
      this.Sum(row);
    }, 500)
  }
};
